.site-header {
  padding: 0 45px 0 15px;
  margin: 0 auto;
  position: fixed;
  right: 0;
  left: 0;
  box-sizing: border-box;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.8);
  height: 60px; }

.menu {
  text-align: right;
  display: inline-block; }

.menu__icon {
  display: none;
  width: 21px;
  height: 15px;
  position: fixed;
  cursor: pointer; }

.menu__icon span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background-color: #f2f2f2;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out; }

.menu__links {
  display: inline-block;
  float: right; }

.menu__links li {
  display: inline-block; }

.menu__icon span:nth-child(1) {
  top: 0; }

.menu__icon span:nth-child(2), .menu__icon span:nth-child(3) {
  top: 7px; }

.menu__icon span:nth-child(4) {
  top: 15px; }

.sub-menu {
  display: none;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  min-width: 200px;
  flex-flow: column nowrap;
  align-items: flex-start; }

.menu__links li:hover .sub-menu {
  display: flex; }

.menu__links li a {
  font-size: 18px;
  line-height: 60px;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  padding: 0 20px;
  text-align: center;
  color: #fff;
  border-radius: 3px; }

.menu.menu_state_open .menu__icon span:nth-child(1) {
  top: 18px;
  width: 0;
  left: 50%; }

.menu.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg); }

.menu.menu_state_open .menu__icon span:nth-child(3) {
  transform: rotate(-45deg); }

.menu.menu_state_open .menu__icon span:nth-child(4) {
  top: 18px;
  width: 0;
  left: 50%; }

.menu.menu_state_open li {
  display: inline-block; }

.menu.menu_state_open li a {
  padding: 15px; }

@media only screen and (max-width: 1060px) {
  .menu__icon {
    display: inline-block;
    position: fixed;
    top: 25px;
    right: 15px; }
  .menu__links {
    display: none; }
  .menu__links li {
    line-height: 30px; }
  .menu_state_open .menu__links {
    margin-top: 60px;
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1000; }
  .menu_state_open .menu__links li {
    display: block;
    text-align: center; }
  .menu_state_open .menu__links .links-block {
    display: block; }
  .sub-menu {
    position: inherit;
    background: none;
    align-items: center; } }

body {
  color: #666; }
